import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import AdmissionManagerProfile from "./AdmissionManagerProfile";
import { useAdmissionDashboardMaster } from "../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import Loading from "../../Loader/Loading";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import AdmissionReadmissionConfig from "./AdmissionReadmission/AdmissionReadmissionConfig";
import AdmissionStudentFormSetting from "./FormSetting/AdmissionStudentFormSetting";
import AdmissionSubjectCriteria from "./AdmissionSubjectCriteria/AdmissionSubjectCriteria";
import AdmissionSubjectCriteriaDetail from "./AdmissionSubjectCriteria/AdmissionSubjectCriteriaDetail/AdmissionSubjectCriteriaDetail";
import AdmissionSubjectCriteriaDetailNested from "./AdmissionSubjectCriteria/AdmissionSubjectCriteriaDetail/AdmissionSubjectCriteriaDetailNested/AdmissionSubjectCriteriaDetailNested";

const FinanceAllStudent = lazy(() =>
  import("../FinanceManager/FinanceAllStudent/FinanceAllStudent")
);

const RequiredDocument = lazy(() =>
  import("./RequiredDocument/RequiredDocument")
);
const AdmissionExport = lazy(() => import("./AdmissionExport/AdmissionExport"));
const AdmissionModerator = lazy(() => import("./Moderator/AdmissionModerator"));
const OneOngoingAdmissionConfig = lazy(() =>
  import("./OneOngoingAdmission/OneOngoingAdmissionConfig")
);
const OngoingAdmission = lazy(() =>
  import("./OngoingAdmission/OngoingAdmission")
);

const CompleteApplication = lazy(() =>
  import("./CompleteApplication/CompleteApplication")
);
const StudentStatitics = lazy(() =>
  import("../FinanceManager/StudentStatitics/StudentStatitics")
);
const StudentStatiticsDetail = lazy(() =>
  import("../FinanceManager/StudentStatitics/StudentStatiticsDetail")
);
const AdmissionCancelled = lazy(() =>
  import("./AdmissionCancelled/AdmissionCancelled")
);
const AdmissionEnquiry = lazy(() =>
  import(
    "../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/AdmissionEnquiry/AdmissionEnquiry"
  )
);

const AdmissionManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { admissionDashboardMaster, admissionDashboardMasterLoading } =
    useAdmissionDashboardMaster({
      data: {
        aid: getQuery.state?.aid,
        mid: "",
      },
      skip: !getQuery.state?.aid,
    });

  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ||
          ["fund"]?.includes(searchParams.get("type")) ? (
            <UserMemberTabsWrapper>
              <AdmissionManagerProfile
                admissionData={{
                  onlineFee: admissionDashboardMaster?.onlineFee,
                  offlineFee: admissionDashboardMaster?.offlineFee,
                  collected_fee: admissionDashboardMaster?.collected_fee,
                  remainingFeeCount:
                    admissionDashboardMaster?.remainingFeeCount,
                  exemptAmount: admissionDashboardMaster?.exemptAmount,
                  requested_status: admissionDashboardMaster?.requested_status,
                }}
                aid={admissionDashboardMaster?._id}
                tabManage={admissionDashboardMaster?.tab_manage}
              />
              {admissionDashboardMasterLoading && <Loading />}
            </UserMemberTabsWrapper>
          ) : searchParams.get("type") === "students" ? (
            <FinanceAllStudent
              instituteId={admissionDashboardMaster?.institute?._id}
              messageId={admissionDashboardMaster?._id}
              headId={admissionDashboardMaster?.admissionAdminHead?._id}
              studentAlias={
                admissionDashboardMaster?.institute?.alias_pronounciation
                  ?.student_alias
              }
              allStudentControl={allStudentControl}
            />
          ) : searchParams.get("type") === "document" ? (
            <RequiredDocument aid={admissionDashboardMaster?._id} />
          ) : searchParams.get("type") === "export" ? (
            <AdmissionExport
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "moderator" ? (
            <AdmissionModerator
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "student-statistics" ? (
            <StudentStatitics
              fid={admissionDashboardMaster?.institute?.financeDepart?.[0]}
            />
          ) : searchParams.get("type") === "statistics-detail" ? (
            <StudentStatiticsDetail />
          ) : searchParams.get("type") === "ongoing" ? (
            <OngoingAdmission
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
              fid={admissionDashboardMaster?.institute?.financeDepart?.[0]}
            />
          ) : searchParams.get("type") === "ongoing-detail" ? (
            <OneOngoingAdmissionConfig
              instituteId={admissionDashboardMaster?.institute?._id}
              aid={admissionDashboardMaster?._id}
            />
          ) : searchParams.get("type") === "cancel-admission" ? (
            <AdmissionCancelled
              studentAlias={
                admissionDashboardMaster?.institute?.alias_pronounciation
                  ?.student_alias
              }
              instituteId={admissionDashboardMaster?.institute?._id}
              aid={admissionDashboardMaster?._id}
            />
          ) : searchParams.get("type") === "complete" ? (
            <CompleteApplication
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "enquiries" ? (
            <AdmissionEnquiry
              admissionId={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "internal" ? (
            <AdmissionReadmissionConfig
              aid={admissionDashboardMaster?._id}
              instituteId={admissionDashboardMaster?.institute?._id}
              studentAlias={
                admissionDashboardMaster?.institute?.alias_pronounciation
                  ?.student_alias
              }
            />
          ) : searchParams.get("type") === "application-form" ? (
            <AdmissionStudentFormSetting />
          ) : searchParams.get("type") === "criteria" ? (
            <AdmissionSubjectCriteria aid={getQuery.state?.aid} />
          ) : searchParams.get("type") === "criteria-detail" ? (
            <AdmissionSubjectCriteriaDetail aid={getQuery.state?.aid} />
          ) : searchParams.get("type") === "criteria-subdetail" ? (
            <AdmissionSubjectCriteriaDetailNested
              aid={getQuery.state?.aid}
              instituteId={admissionDashboardMaster?.institute?._id}
            />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default AdmissionManager;

const allStudentControl = {
  collectAccess: "Other",
  govtAccess: "Other",
  cancelAccess: false,
  editStructureControl: "unauthorized",
  editGovtNumberContorl: "unauthorized",
  editInstallmentControl: "unauthorized",
  editGovtInstallmentControl: "unauthorized",
};
